.side-bar {
    padding: var(--large-indent);
    min-width: 35rem;
    background-color: var(--color-positive);
    
    h2 {
        border-bottom: 1px solid #000;
    }

    &__list {
        padding: var(--medium-indent) 0;
        display: flex;
        flex-direction: column;
        gap: var(--small-indent);
    }
    
    &__item {
        border-radius: var(--radius);
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 var(--small-indent);
        cursor: pointer;

        a:link, a:visited {
            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            text-decoration: none;
            color: var(--color-negative);
            padding: var(--xsmall-indent) var(--small-indent);
            font-size: 1.8rem;
            cursor: pointer;
            display: block;
            font-weight: 600;
            flex-grow: 1;
        }

        &:hover {
            background-color: var(--color-primary-light);
        }
    }
}
