:root {
    --element-gradient: linear-gradient(to right, #12e952, #d6ed71, #f64f59);
    --xlarge-indent: 5rem;
    --large-indent: 3rem;
    --medium-indent: 2rem;
    --small-indent: 1rem;
    --xsmall-indent: 0.5rem;
    --color-background: #eee;
    --color-primary: #2abd67;
    --color-primary-dark: #1f8d4d;
    --color-primary-light: #2abd6770;
    --color-positive: #fff;
    --color-negative: #374958;
    --radius: 8px;
    --priority-high: #F72119;
    --priority-medium: #FFF01F;
    --priority-low: #1FFF0F;
}