html {
    font-size: 62.5%;
    min-height: 100%;
}

body {
    font-size: 1.6rem;
    font-family: "Ubuntu", sans-serif;
    color: var(--color-negative);
    // background-image: url("img/background-art.jpg");
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.container {
    max-width: 135rem;
    margin: 0 auto;
    min-height: 100%;
}

.main {
    display: flex;
}

.hidden {
    display: none;
}

// HEADINGS

h2 {
    font-weight: 700;
}

h1 {
    font-size: 3.6rem;
}

