// DYNAMIC CLASSES

.selected {
    background-color: var(--color-primary-light);
    color: var(--color-positive);
}

.hidden {
    display: none !important;
}

.checked {
    opacity: 0.5;

    * {
        text-decoration: line-through;
    }
}

// BUTTONS

button {
    cursor: pointer;
    border: none;
    background: none;
    font-weight: 500;
    font-family: inherit;
    color: inherit;
    font-size: 1.8rem;
}

.form-btn {
    display: flex;
    justify-content: center;
    gap: var(--medium-indent);
    margin-top: var(--xsmall-indent);

    &__add, &__cancel {
        color: var(--color-positive);
        padding: var(--xsmall-indent) var(--small-indent);
        border-radius: var(--radius);
    }

    &__add {
        background: var(--color-primary);
    }
    
    &__cancel {
        background: rgb(190, 4, 4);
    }
}

.btn-open-form {
    padding: var(--xsmall-indent) var(--small-indent);
}

.edit {
    position: relative;

    &__open {
        transform: translateY(10%);
    }

    &__options {
        position: absolute;
        top: -7.4rem;
        left: -7rem;
        z-index: 100;
        
        background-color: var(--color-background);
        box-shadow: 0 0 5px var(--color-negative);
        color: var(--color-negative);

        display: flex;
        flex-direction: column;
        
        & > * {
            text-align: left;
            padding: var(--xsmall-indent);
            width: 100%;

        }

        & > *:hover {
            background-color: azure;
        } 
    }
}

// INPUtS

.input {
    font-family: inherit;
    font-weight: 600;
    color: inherit;
    padding: var(--xsmall-indent);
    border: 2px solid var(--color-negative);
    width: 100% !important;
}

textarea {
    resize: vertical;
    font-weight: 500;
}
