.content {
    padding: var(--large-indent) var(--xlarge-indent);
    background: var(--color-background);
    flex-grow: 1;

    h1 {
        background-color: var(--color-primary);
        color: var(--color-positive);
        padding: var(--medium-indent);
        border-radius: var(--radius);
    }

    .todo-list {
        display: flex;
        flex-direction: column;
        gap: var(--small-indent);
        margin-top: var(--medium-indent);
    }

    .todo-form {
        background-color: var(--color-positive);
        margin-top: var(--small-indent);
        padding: var(--medium-indent);

        display: flex;
        flex-direction: column;

        & > *:nth-child(odd) {
            margin-top: var(--xsmall-indent);
        }

        &__priority {
            background: #039c0f;
            padding: 0;
        }

        &__priority::-webkit-slider-thumb {
            -webkit-appearance: none;
            background: #039c0f;
        }

        &__priority::-webkit-slider-runnable-track {
            // background: linear-gradient(to right, var(--priority-low), var(--priority-medium), var(--priority-high));
        }
    }

    .todo {
        cursor: pointer;
        display: flex;
        align-items: flex-start;
        gap: var(--xsmall-indent);

        border: 1px solid #000;
        border-radius: 10px 0 0 10px ;
        padding: var(--small-indent);

        &__complete {
            cursor: pointer;
            min-height: 1.8rem;
            min-width: 1.8rem;
            border: 1px solid #000;
        }

        &__text {
            flex-grow: 1;
            line-height: 1;
        }

        &__date {
            min-width: 10rem;
            border: 2px solid var(--color-negative);
            border-radius: 20px;
            padding: 0.5rem
        }
    }


    .todo:hover {
        background-color: var(--color-positive);
    }
    
    .priority-high {
        border-left: 10px solid var(--priority-high);   
    }  
    .priority-medium {
        border-left: 10px solid var(--priority-medium);;   
    } 
    .priority-low {
        border-left: 10px solid var(--priority-low);;
    } 
}