.header {
    height: 7rem;
    background: var(--color-background);
    padding: var(--small-indent) var(--medium-indent);
    border-bottom: 2px solid var(--color-primary-dark);

    display: flex;
    justify-content: space-between;

    &__logo {
        display: flex;
        align-items: center;
    }

    .logo-text {
        font-size: 4.4rem;
        font-weight: 700;

        span {
            color: var(--color-primary);
        }
    }

    .hide-side-bar {
        padding: 3px;
        span {
            display: block;
            height: 9px;
            border-bottom: 2px solid var(--color-negative);
            border-top: 2px solid var(--color-negative);;
        }
    }
}